import React from 'react'

//Styles
import "../index.css"

const GifGridItem = ( {id, title, url} ) => {

    console.log(id, title, url);
    return (
        <div className="card animate__animated animate__fadeIn">
            <img src={url} alt={title}></img>
            <p>{title}</p>
        </div>
    )
}

export default GifGridItem
